// import {
//   SWITCH_DIALOG
// } from './mutation_type'
export default {
  state: {
    data: 'dialog',
    show: false,
    imgUrl: 'https://ss0.baidu.com/73x1bjeh1BF3odCf/it/u=3206528002,1065096090&fm=85&s=0C38729306A405AB5A1CA07903008076',
    num: 0,
    products: true,
    listName: null
  },
  // gettersvuecomputedstate
  // show
  getters: {
    not_show (state) {
      return !state.show
    },
    now_num (state) {
      return state.num
    },
    now_products (state) {
      return {
        num: state.num,
        products: state.products
      }
    },
    ListName (state) {
      return state.listName
    }
  },
  mutations: {
    switch_dialog (state) { // statestate
      state.show = !state.show
    },
    increase (state) {
      state.num = state.num + 1
    },
    decrease (state) {
      if (state.num <= 0) {
        state.num = 0
      } else {
        state.num = state.num - 1
      }
    },
    delete (state) {
      state.num = 0
      state.products = false
    },
    // mapMutation
    setListValue (state, value) {
      state.listName = value + '1'
    }
  },
  actions: {
    switch_dialog ({ commit }) { // context$store
      commit('switch_dialog')
      // mutations
    },
    increase (context) {
      context.commit('increase')
    },
    decrease (context) {
      context.commit('decrease')
    }
  }
}



