import Vue from 'vue'
import vuex from 'vuex'
import dialogStore from './dialog_store.js' // js
import moduleB from './moduleB.js'

Vue.use(vuex)

// store  => vuexmodules
// export default new vuex.Store({
//   state: {
//     show: false // $store.state.show
//   }
// })

// 

export default new vuex.Store({
  modules: {
    dialog: dialogStore,
    moduleB: moduleB
  }
})



